import React, { useState } from 'react';
import HomeIcon from '@material-ui/icons/Home';
import  FeaturedPlayListOutlinedIcon  from '@material-ui/icons/FeaturedPlayListOutlined';
import  AssignmentTurnedInRoundedIcon  from '@material-ui/icons/AssignmentTurnedInRounded';
import  PeopleAltOutlinedIcon  from '@material-ui/icons/PeopleAltOutlined';
import NotificationImportantOutlinedIcon  from '@material-ui/icons/NotificationImportantOutlined';
import SearchIcon  from '@material-ui/icons/Search';
import {Avatar, Button, Input} from '@material-ui/core';
import LanguageIcon from '@material-ui/icons/Language';
import "../css/Navbar.css";
import { useSelector } from 'react-redux';
import { selectUser } from '../features/userSlice';
import db, { auth } from '../firebase';
import  Modal  from 'react-modal'
import { ExpandMore, SportsRugbySharp } from '@material-ui/icons';
import LinkIcon from '@material-ui/icons/Link';
import firebase from 'firebase';




function Navbar() {


    const user = useSelector(selectUser);
    const [openModal, setOpenModal] = useState(false);
    const [input, setInput] = useState("")
    const [inputUrl, setInputUrl] = useState("")

    const handleQuestion = (e) =>{
        e.preventDefault();
        setOpenModal(false);

        db.collection('questions').add({
            question: input,
            imageUrl: inputUrl,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            user : user,
        });

        setInput("");
        setInputUrl("");
    }

    return(
        <div className = "qHeader">
            <div className = "qHeader__logo">
              <img
              src = 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/91/Quora_logo_2015.svg/250px-Quora_logo_2015.svg.png'
              alt = ""
              />  
            </div>
            <div className = "qHeader__icons">
            <div className = "qHeader__try">
              
                </div>
                <div className = "qHeader__icon">
                    <HomeIcon/>
                </div>
                <div className = "qHeader__icon">
                  <FeaturedPlayListOutlinedIcon/>  
                </div>
                <div className = "qHeader__icon">
                    <AssignmentTurnedInRoundedIcon/>
                </div>
                <div className = "qHeader__icon">
                    <PeopleAltOutlinedIcon/>
                </div>
                <div className = "qHeader__icon">
                    <NotificationImportantOutlinedIcon />
                </div>
            </div>
            <div className = "qHeader__input">
                <SearchIcon />
                <input type = "text" placeholder = "Search Quora"/>
            </div>
            <div className = "qHeader__Rem">
                <div className = "qHeader__avater">
                  <Avatar onClick ={() => auth.signOut()}  src = {user.photo}/>  
                </div>
                <LanguageIcon />
                <Button onClick ={() => setOpenModal(true)}>Add Question</Button>
                <Modal
                    isOpen = {openModal}
                    onRequestClose = {() => setOpenModal(false)}
                    shouldCloseOnOverlayClick = {false}
                    style = {{
                        overlay:{
                            width: 700,
                        height: 600,
                        backgroundColor: "rgba(0,0,0,0.8)",
                        zIndex: "1000",
                        top: "50%",
                        left: "50%",
                        marginTop: "-300px",
                        marginLeft: "-350px"
                        },
                       
                    }}
                >
                <div className = "modal_title">
                    <h5>Add Question</h5>
                    <h5>Share Link</h5>
                </div>
                    <div className = "modal_info">
                        <Avatar
                            className = "avatar"
                            src = {user.photo}
                         />
                         <p>{user.displayName ? user.displayName : user.email} asked</p>
                         <div className = "molad_scope">
                             <PeopleAltOutlinedIcon />
                             <p>Public</p>
                             <ExpandMore />
                         </div>
                    </div>
                    <div className = "modal_field">
                        <Input 
                            required
                            value = {input}
                            onChange= {(e) => setInput(e.target.value)}
                            type = "text"
                            placeholder = "Start Your Question"
                        />
                    </div>
                    <div className = "modal_fieldLink">
                    <LinkIcon />
                        <Input 
                            value = {inputUrl}
                            onChange= {(e) => setInputUrl(e.target.value)}
                            type = "text"
                            placeholder = "Optional, Give a link"
                        />
                    </div>
                    <div className = "modal_buttons">
                    <button onClick={() => setOpenModal(false)}>Close</button>
                    <button onClick = {handleQuestion} type = "submit" className = "add">Add Question</button> 
                    </div>
                    
                
                </Modal>
            </div>

        </div>
    );
}

export default Navbar;